/*viewbox*/
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  display: grid;
  justify-content: center; }

a {
  color: #f5e2cd;
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

.body {
  min-width: 320px;
  font-family: "roboto-regular", "Georgia", serif;
  color: #f5e2cd;
  height: 100vh;
  margin: 0;
  padding: 0;
  cursor: default;
  background-color: #39383f; }
  @media (min-width: 768px) {
    .body {
      min-width: 768px; } }
  @media (min-width: 1200px) {
    .body {
      width: 1200px; } }

.body__cards {
  margin-top: 20px; }

.body__footer {
  margin-top: 50vh; }

.nav {
  display: none;
  position: fixed;
  width: 320px;
  height: 105%;
  background-color: #ff6347;
  z-index: 10;
  animation: menu-in ease-in 0.5s forwards; }

.nav__list {
  display: block;
  margin-top: 150px;
  margin-right: 30px;
  flex-direction: column;
  border-right: 1px solid #f5e2cd; }

.nav__item {
  font-size: 20px;
  padding-left: 30px;
  margin: 0;
  margin-bottom: 10px;
  text-transform: uppercase;
  height: 40px;
  box-sizing: border-box; }
  .nav__item a:hover,
  .nav__item a:focus {
    outline: none;
    border-bottom: 2px solid #f5e2cd;
    animation: opacity ease 0.5s forwards; }
  .nav__item a:active {
    color: #413d4c;
    border-bottom: none; }

.nav__button-close {
  width: 30px;
  height: 2px;
  border: none;
  background-color: #f5e2cd;
  position: relative;
  left: 35px;
  top: 30px;
  transform: rotate(45deg);
  cursor: pointer; }
  .nav__button-close::before {
    content: "";
    position: absolute;
    background-color: #f5e2cd;
    width: 30px;
    height: 2px;
    top: 0;
    left: 0;
    transform: rotate(90deg); }
  .nav__button-close:hover, .nav__button-close:hover::before {
    background-color: #413d4c;
    transition-duration: 1s; }
  .nav__button-close:focus, .nav__button-close:focus::before {
    background-color: #f5e2cd; }

@keyframes menu-in {
  0% {
    left: -100px; }
  50% {
    left: 0; }
  100% {
    left: 0; } }

@keyframes opacity {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.8; }
  100% {
    opacity: 0.6; } }

.header {
  min-width: 320px;
  height: 100px;
  background-color: #413d4c;
  border-radius: 10px; }
  @media (min-width: 768px) {
    .header {
      min-width: 768px; } }
  @media (min-width: 1200px) {
    .header {
      width: 1200px; } }

.header__wrap {
  position: relative;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  padding-top: 20px; }

.header__button-open {
  width: 30px;
  height: 2px;
  background-color: #ff6347;
  border: none;
  border-radius: 10px;
  position: relative;
  margin-left: 50px;
  margin-top: 25px;
  cursor: pointer; }
  .header__button-open::after {
    content: "";
    position: absolute;
    width: 30px;
    top: 5px;
    left: 0;
    height: 2px;
    background-color: inherit;
    border-radius: inherit; }
  .header__button-open::before {
    content: "";
    position: absolute;
    width: 30px;
    top: -5px;
    left: 0;
    height: 2px;
    border-radius: inherit;
    background-color: inherit; }
  .header__button-open:hover, .header__button-open:hover::before {
    background-color: rgba(240, 207, 173, 0.58);
    transition-duration: 0.5s; }
  .header__button-open:focus, .header__button-open:focus::before {
    background-color: #f5e2cd; }

.header__logo {
  fill: #ff6347; }

.main {
  height: 100%; }

.main__wrap {
  display: flex;
  flex-direction: column;
  align-items: center; }

.main__button {
  width: 200px;
  height: 50px;
  font-size: 18px;
  margin-top: 30px;
  color: #ffffff;
  background-color: #ff6347;
  border-radius: 10px;
  border: none;
  transition-duration: 0.25s; }
  .main__button:hover, .main__button:active {
    background-color: #413d4c; }
  .main__button:focus {
    background-color: #413d4c;
    border: 2px solid #ff6347; }
  .main__button:disabled {
    opacity: 0.3; }

.main__form {
  margin-top: 20px;
  padding: 0 30px 30px 30px; }

.form {
  display: flex;
  width: 300px;
  flex-direction: column;
  border-radius: 10px;
  border: 2px solid rgba(240, 207, 173, 0.58);
  background-color: #413d4c;
  animation: opacity ease-in-out alternate 1s forwards; }

.form__title {
  width: 300px;
  position: relative;
  text-align: center;
  margin-bottom: 35px; }
  .form__title::after {
    content: "";
    width: 360px;
    position: absolute;
    top: 35px;
    left: -30px;
    border-bottom: 1px solid #413d4c; }

.form__input {
  color: #f5e2cd;
  font-size: 18px;
  height: 40px;
  margin-bottom: 20px;
  border-radius: 10px;
  padding: 0 10px;
  background-color: rgba(240, 207, 173, 0.58);
  border: none;
  box-sizing: border-box;
  transition-duration: 0.5s; }
  .form__input:hover {
    border: 3px solid #ff6347; }
  .form__input:focus {
    outline: none;
    border: 3px solid #39383f; }

.form__label {
  margin-bottom: 5px; }

.form__input--file {
  display: none; }

.form__upload-file {
  width: 300px;
  margin-top: 10px;
  margin-bottom: 30px;
  box-sizing: border-box;
  transition-duration: 0.5s; }

.form__label-file-upload {
  color: #f5e2cd;
  font-size: 18px;
  width: 165px;
  background-color: rgba(240, 207, 173, 0.58);
  border: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 9px 5px 8px;
  cursor: default; }

.form__label-file {
  background-color: #ff6347;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 10px 27px 9px;
  color: #f5e2cd;
  transition-duration: 0.5s;
  cursor: pointer; }
  .form__label-file:hover {
    background-color: #39383f; }
  .form__label-file:focus {
    outline: none;
    padding: 9px 27px 9px;
    border: 1px solid #ff6347;
    background-color: #39383f; }
  .form__label-file:active {
    background-color: #39383f; }

.form__textarea {
  height: 80px;
  margin-bottom: 20px;
  resize: none;
  border-radius: 10px;
  padding: 5px;
  background-color: rgba(240, 207, 173, 0.58);
  box-sizing: border-box;
  color: #f5e2cd;
  font-size: 18px;
  transition-duration: 0.5s; }
  .form__textarea:hover {
    border: 3px solid #ff6347; }
  .form__textarea:focus {
    outline: none;
    border: 3px solid #39383f; }

.form__wrap-checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 50px; }

.form__checkbox {
  display: none; }

.form__label--checkbox {
  margin-left: 30px;
  position: relative;
  cursor: pointer; }
  .form__label--checkbox::before {
    content: "";
    position: absolute;
    left: -30px;
    top: -2px;
    width: 20px;
    height: 20px;
    border: 2px solid #ff6347;
    background-color: transparent;
    border-radius: 5px; }

.form__checkbox:checked + .form__label--checkbox::before {
  content: "";
  position: absolute;
  background-color: #ff6347; }

.form__checkbox:checked + .form__label--checkbox::after {
  content: "Г";
  font-family: sans-serif;
  font-weight: bold;
  color: #413d4c;
  transform: rotate(-145deg);
  position: absolute;
  left: -32px;
  top: -5px;
  width: 20px;
  height: 20px;
  border-radius: 5px; }

.form__button-wrap {
  display: flex;
  justify-content: space-between; }

.form__button {
  background-color: #ff6347;
  color: #ffffff;
  width: 140px;
  height: 40px;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  transition-duration: 0.5s; }
  .form__button:hover, .form__button:active {
    background-color: #39383f; }
  .form__button:focus {
    background-color: #39383f;
    border: 2px solid #ff6347; }
  .form__button:disabled {
    opacity: 0.3; }

@keyframes opacity {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.cards {
  min-width: 320px;
  height: 100%; }
  @media (min-width: 768px) {
    .cards {
      min-width: 768px;
      display: grid;
      grid-template-columns: 1fr 1fr; } }
  @media (min-width: 1200px) {
    .cards {
      width: 1200px;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto; } }

.cards__wrap {
  margin: 30px auto;
  width: 300px;
  background-color: #413d4c;
  border-top: 3px solid #ff6347;
  transition-duration: 0.25s; }
  .cards__wrap:hover {
    box-shadow: 0 0 20px 1px #ff6347; }

.cards__title {
  text-align: center;
  padding: 10px; }

.cards__description {
  margin: 20px;
  padding: 10px;
  height: 30px;
  border-left: 3px solid #ff6347;
  position: relative;
  overflow: hidden; }
  .cards__description::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    bottom: 0;
    left: -9px;
    background-color: #ff6347;
    border-radius: 100%; }

.footer {
  min-width: 320px;
  height: 100px;
  font-size: 20px;
  text-align: center;
  border-top: 3px solid rgba(240, 207, 173, 0.58); }
  @media (min-width: 768px) {
    .footer {
      min-width: 768px;
      text-align: right; } }
  @media (min-width: 1200px) {
    .footer {
      width: 1200px; } }
  .footer div {
    display: inline-block;
    margin: 20px 20px;
    border-bottom: 3px solid #f5e2cd; }
    @media (min-width: 768px) {
      .footer div {
        margin: 10px 40px; } }

.footer__email {
  color: #ff6347;
  border-bottom: 3px solid #ff6347;
  transition-duration: 0.5s; }
  .footer__email:hover {
    color: #f5e2cd; }
  .footer__email:focus {
    outline: none;
    color: #f5e2cd;
    border-bottom: 3px solid #f5e2cd; }
  .footer__email:active {
    border-bottom: 3px solid #f5e2cd; }
